import { useState } from "react";
import { Desc, ToggleContainer } from "../styles/common/ToggleContainerButton";

const Button = () => {
  const [isOn, setIsOn] = useState(false);

  const toggleHandler = () => {
    // isOn    
    setIsOn(!isOn);
  };

  return (
    <>
      <ToggleContainer
        //    (isOn) boolean    
        onClick={toggleHandler}
      >
        {/*  div  2 .   'toggle-container', 'toggle-circle'   */}
        {/* Toggle Switch ON   toggle--checked  div  2  .   */}
        <div
          className={`toggle-container ${isOn ? "toggle--checked" : null}`}
        />
        <div className={`toggle-circle ${isOn ? "toggle--checked" : null}`} />
      </ToggleContainer>
      {/* Desc  */}
      {/* Toggle Switch ON   Desc    'Toggle Switch ON',    'Toggle Switch OFF'.   . */}
      {isOn === false ? (
        <Desc>
          <div className="OFF"> OFF</div>
        </Desc>
      ) : (
        <Desc>
          <div className="ON"> ON</div>
        </Desc>
      )}
    </>
  );
};

export default Button;
