import React from "react";
import { NavLink } from "react-router-dom";
import "../styles/common/Nav.css";

const Nav = () => {
  return (
    <nav>
      <div>
        <NavLink to="/"></NavLink>
      </div>
      <div>
        <NavLink to="/water1"></NavLink>
      </div>
      <div>
        <NavLink to="/water2">1</NavLink>
      </div>
      <div>
        <NavLink to="/water3">2</NavLink>
      </div>
    </nav>
  );
};

export default Nav;
